$color: #50d2c2;
$headBg: #3d3d3d;
$headNavSelect: #383838;

%clearfix {
  &:after {
    content: '';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
    font-size: 0;
  }
}

.header span {
  display: inline;
  font-size: 14px;
  color: #fff;
  line-height: 60px;
}

.header span a {
  color: $color;
}

.header span em {
  color: #fff;
  padding-right: 3px;
}

.header p.p-tel {
  color: #fff;
  font-size: 14px;
  padding-right: 10px;
}

i.white-tel {
  display: inline-block;
  float: left;
  margin: 23px 5px 0 0;
  width: 15px;
  height: 15px;
  background: url(../images/white-tel.png) no-repeat;
}

.nav {
  // width: 550px;
  width: 70%;
  min-width: 650px;
  margin: 0 auto;
  text-align: center;
}

.nav li {
  min-width: 70px;

  /**/
  height: 56px;
  //height: 60px;
  /**/

  position: relative;
  font-size: 18px;
  text-align: center;
  display: inline-block;
  /**/
  //
  line-height: 52px;
  /**/
}

